



<template>
  <div>
    <!--Stats cards-->
    <div class="row">
      <div
        class="col-md-6 col-xl-3"
        v-for="stats in statsCards"
        :key="stats.title"
      >
        <stats-card>
          <div
            class="icon-big text-center"
            :class="`icon-${stats.type}`"
            slot="header"
          >
            <i :class="stats.icon"></i>
          </div>
          <div class="numbers" slot="content">
            <p>{{ stats.title }}</p>
            {{ stats.value }}
          </div>
          <div class="stats" slot="footer">
            <i :class="stats.footerIcon"></i> {{ stats.footerText }}
          </div>
        </stats-card>
      </div>
    </div>

    <!--Charts-->
    <div class="row">
        

      <div class="col-md-6 col-12">
        <chart-card
          title="Guias com mais tours alocados"
          sub-title="Entre todos os guias"
          :chart-data="preferencesChart.data"
          chart-type="Pie"
        >
          <span slot="footer">
            <i class="ti-timer"></i> Guias com mais tours alocados</span
          >
          <div slot="legend">
            <i class="fa fa-circle text-info"></i> João Almeida
            <i class="fa fa-circle text-danger"></i> Paulo Antunes
            <i class="fa fa-circle text-warning"></i> Joaquim ABC
          </div>
        </chart-card>
      </div>

      
    </div>
  </div>
</template>
<script>
import { StatsCard, ChartCard } from "@/components/index";
import Chartist from "chartist";
import { METHODS } from "http";
export default {
  components: {
    StatsCard,
    ChartCard,
  },
  /**
   * Chart data used to render stats, charts. Should be replaced with server data
   */
  data() {
    return {
      statsCards: [
        
        {
          type: "success",
          icon: "ti-wallet",
          title: "Receita total",
          value: "",
          footerText: "Last day",
          footerIcon: "ti-calendar",
        },
        {
          type: "danger",
          icon: "ti-calendar",
          title: "Número de Reservas",
          value: "",
          footerText: "In the last hour",
          footerIcon: "ti-timer",
        },
        {
          type: "info",
          icon: "ti-user",
          title: "Número de Guias",
          value: "",
          footerText: "Updated now",
          footerIcon: "ti-reload",
        },
      ],
      usersChart: {
        data: {
          labels: [
            "9:00AM",
            "12:00AM",
            "3:00PM",
            "6:00PM",
            "9:00PM",
            "12:00PM",
            "3:00AM",
            "6:00AM",
          ],
          series: [
            [287, 385, 490, 562, 594, 626, 698, 895, 952],
            [67, 152, 193, 240, 387, 435, 535, 642, 744],
            [23, 113, 67, 108, 190, 239, 307, 410, 410],
          ],
        },
        options: {
          low: 0,
          high: 1000,
          showArea: true,
          height: "245px",
          axisX: {
            showGrid: false,
          },
          lineSmooth: Chartist.Interpolation.simple({
            divisor: 3,
          }),
          showLine: true,
          showPoint: false,
        },
      },
      activityChart: {
        data: {
          labels: [
            "Jan",
            "Feb",
            "Mar",
            "Apr",
            "Mai",
            "Jun",
            "Jul",
            "Aug",
            "Sep",
            "Oct",
            "Nov",
            "Dec",
          ],
          series: [
            [542, 543, 520, 680, 653, 753, 326, 434, 568, 610, 756, 895],
            [230, 293, 380, 480, 503, 553, 600, 664, 698, 710, 736, 795],
          ],
        },
        options: {
          seriesBarDistance: 10,
          axisX: {
            showGrid: false,
          },
          height: "245px",
        },
      },
      preferencesChart: {
        data: {
          labels: ["62%", "32%", "6%"],
          series: [62, 32, 6],
        },
        options: {},
      },
    };
  },
  created() {
    this.calcularNumeroGuias();
    this.calcularNumeroReservas();
    this.calcularLucro();
  },

  methods: {
    calcularNumeroGuias() {
      const guiasData = JSON.parse(localStorage.getItem("guias")) || [];
      const numeroGuias = guiasData.length;
      this.$set(this.statsCards[2], 'value', numeroGuias);
    },
    calcularNumeroReservas() {
      const reservasData = JSON.parse(localStorage.getItem("reservas")) || [];
      const numeroReservas = reservasData.length;
      this.$set(this.statsCards[1], 'value', numeroReservas);
    },
    calcularLucro() {
  const reservasData = JSON.parse(localStorage.getItem("reservas")) || [];

  let totalValue = 0;
  for (const reserva of reservasData) {
    for (const event of reserva.events) {
      const preco = parseFloat(event.preco.replace(",", "."));
      totalValue += preco;
    }
  }

  this.$set(this.statsCards[0], "value", totalValue);
},


  }
};
</script>
<style></style>
